@import "../../styles/default.scss";

.badge {
    margin: 0 4px;
    height: 20px;
    padding: 0 8px;
    border-radius: 4px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-size: 12px;

    &.default {
        background-color: #C4C4C4;
    }

    &.error {
        background-color: #FF0000;
    }

    &.warning {
        background-color: #FFA500;
    }

    &.good {
        background-color: #56A513;
    }

    &.circle {
        border-radius: 50px;
    }

    &.large {
        height: 32px;
    }
}