@import "../../styles/default.scss";

.card {
    text-decoration: none;
    &.card-station {
        position: relative;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        background-color: #C2CDD6;
        border-radius: 16px;
        padding: 16px;
        padding-top: 32px;
        margin: 8px;
        margin-top: 24px;
        max-width: 328px;
        box-sizing: border-box;

        & .station-name {
            position: absolute;
            top: -15px;
            width: 150px;
            height: 30px;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: #DBFF00;
            border-radius: 8px;
            font-weight: bold;
            font-size: 18px;
            color: #8E8E8E;
            text-transform: uppercase;
        }

        & .card-container {
            display: grid;
            grid-gap: 8px;
            grid-template-columns: repeat(2, 1fr);

            & .information-tag {
                width: 140px;
                height: 140px;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                color: #56A513;
                border: 2px solid #56A513;
                border-radius: 18px;
                font-size: 18px;
                font-weight: bold;
                background-color: #fff;
                text-transform: uppercase;

                & .value {
                    font-size: 36px;
                }
            }
        }
    }
}