table {
    border-collapse: collapse; 
    border: 3px solid #8E8E8E;

    & tr {
        border-bottom: 1px solid #8E8E8E;

        &:first-child {
            border-bottom: 3px solid #8E8E8E;
        }

        & th, td {
            padding: 20px 20px;
            border-right: 1px solid #8E8E8E;

            &:last-child {
                border: none;
            }
        }
    }
}