@import "../../styles/default.scss";

.view.with-nav.station {
    & header.view-header {
        padding: 23px 0;
        display: flex;
        align-items: center;
        border-bottom: 1px solid #C2CDD6;

        & .header-spacing {
            flex: 1;
        }

        & .end {
            display: flex;
            align-items: center;
            justify-content: center;
        }

        & .header-tab-btn {
            margin: 0 12px;
            cursor: pointer;

            &.active .icon {
                color: #1a6aa7;
            }

            & .icon {
                font-size: 20px;
            }
        }
    }

    & .container {
        margin-top: 32px;
        
        & table {
            width: 100%;
        }
    }
}