@import "../../styles/default.scss";

.navigation-bar {
    top: 0;
    left: 0;
    position: fixed;
    width: 100%;
    height: 60px;
    background: #002c4db9;
    backdrop-filter: blur(20px) saturate(180%);
    z-index: 9999999;

    & ul {
        display: flex;
        list-style-type: none;
        height: 100%;
        padding: 0 96px;
        margin: 0;
        box-sizing: border-box;

        & li {
            cursor: pointer;

            &.active {
                background-color: #2F8ED5;
            }

            & a {
                text-decoration: none;
                display: flex;
                align-items: center;
                justify-content: center;
                color: #fff;
                width: 184px;
                height: 100%;

                & .icon {
                    margin-right: 10px;
                    font-size: 20px;
                }
    
                & span {
                    text-decoration: none;
                    font-size: 15px;
                }
            }
        }
    }
}